        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'
import CTableWrapper from './base/Table.vue'

export default {
  name: 'InvoiceCorrection',
  components: { CTableWrapper, PulseLoader },
  computed: {
    invoiceLoading() { return !this.$store.state.invoiceLoaded},
    invoices() { return this.$store.state.invoices }
  },
  mounted: function() {
    if(this.$store.state.invoices.length === 0){
      this.load();
    }
    
  },
  methods: {        
    edit(index){      
