      this.$store.commit('setIsInvoicePublishedError', false)
      this.$router.push('/invoiceedition/' + index)      
    },
    load(){
      this.$store.dispatch('fetchInvoicesToAnalyze');    
    },   
    show(){
      console.log(this.$store.state.invoices);
    },
    getShuffledUsersData () {
      return this.shuffleArray(usersData.slice(0))
    }
  }
}
</script>
